import {uniHttp} from "../../apis/api.js"
export default {
  data() {
    return {
      orderStatus: '',
      orderTYVoList: [],
      hidden: false,
      repeatedLoading: true,
      page: 1,
      flag: ''
    };
  },

  components: {},
  props: {},

  /**
   * 生命周期函数--监听页面加载
   */
  onLoad: function (options) {
    this.setData({
      orderStatus: options.orderStatus
    });

    if (options.orderStatus == 1) {
      uni.setNavigationBarTitle({
        title: '待付款'
      });
    } else if (options.orderStatus == 2) {
      uni.setNavigationBarTitle({
        title: '待发货'
      });
    } else if (options.orderStatus == 3) {
      uni.setNavigationBarTitle({
        title: '待收货'
      });
    } else if (options.orderStatus == 5) {
      uni.setNavigationBarTitle({
        title: '待评价'
      });
    }
  },

  /**
   * 生命周期函数--监听页面初次渲染完成
   */
  onReady: function () {},

  /**
   * 生命周期函数--监听页面显示
   */
  onShow: function () {
    uni.hideShareMenu();
    this.setData({
      page: 1,
      orderTYVoList: []
    });
    this.getOrderList();
    this.pageLoading = false;
  },

  /**
   * 生命周期函数--监听页面隐藏
   */
  onHide: function () {},

  /**
   * 生命周期函数--监听页面卸载
   */
  onUnload: function () {},

  /**
   * 页面相关事件处理函数--监听用户下拉动作
   */
  onPullDownRefresh: function () {},

  /**
   * 页面上拉触底事件的处理函数
   */
  onReachBottom: function () {
    if (this.repeatedLoading) {
      this.repeatedLoading = false;
      this.page = this.page + 1;
      this.getOrderList();
    }
  },

  /**
   * 用户点击右上角分享
   */
  onShareAppMessage: function () {},
  methods: {
    orderDetails(e) {
      if (!this.pageLoading) {
        this.pageLoading = true;
        uni.navigateTo({
          url: '/pages/orderDetailByOrders/orderDetailByOrders?parendNo=' + e.currentTarget.dataset.parendno + '&supId=' + e.currentTarget.dataset.supid + '&orderStatus=' + e.currentTarget.dataset.orderstatus
        });
      }
    },

    logistics(e) {
      var that = this;
      uni.request({
        url: getApp().globalData.url3 + getApp().globalData.getLogisticsDetailById + e.currentTarget.dataset.id,
        data: '',
        header: {
          aid: 129
        },
        method: 'GET',
        dataType: 'json',
        responseType: 'text',
        success: function (res) {
          if (!that.pageLoading) {
            that.pageLoading = true;
            uni.navigateTo({
              url: '/pages/orderTracking/orderTracking?logisticsCode=' + res.data.data.rtOrderDetail.logisticsCode + '&logisticsPostid=' + res.data.data.rtOrderDetail.logisticsPostid
            });
          }
        },
        fail: function (res) {},
        complete: function (res) {}
      });
    },

    deletOrder(e) {
      var that = this;
      uni.showModal({
        title: '提示',
        content: '确定删除订单',
        success: function (res) {
          if (res.confirm) {
            if (e.currentTarget.dataset.orderstatus == 7) {
              that.setData({
                hidden: true
              });
              uni.request({
                url: getApp().globalData.url3 + getApp().globalData.deleteOrderByCondition,
                data: {
                  parendNo: e.currentTarget.dataset.parendno,
                  supId: e.currentTarget.dataset.supid,
                  category: 0
                },
                header: {
                  aid: 127
                },
                method: 'delete',
                dataType: 'json',
                responseType: 'text',
                success: function (res) {
                  that.setData({
                    page: 1,
                    orderTYVoList: []
                  });
                  that.getOrderList();
                },
                fail: function (res) {},
                complete: function (res) {
                  that.setData({
                    hidden: false
                  });
                }
              });
            } else if (e.currentTarget.dataset.orderstatus == 6) {
              uni.request({
                url: getApp().globalData.url3 + getApp().globalData.deleteOrderByCondition,
                data: {
                  parendNo: e.currentTarget.dataset.parendno,
                  supId: e.currentTarget.dataset.supid,
                  category: 1
                },
                header: {
                  aid: 127
                },
                method: 'delete',
                dataType: 'json',
                responseType: 'text',
                success: function (res) {
                  that.setData({
                    page: 1,
                    orderTYVoList: []
                  });
                  that.getOrderList();
                },
                fail: function (res) {},
                complete: function (res) {}
              });
            }
          } else if (res.cancel) {}
        }
      });
    },

